/* ===================================
	 main
	 @mixin ================================== */
.main {
	&_home {
		margin-top: -150px; } }

/* ===================================
	 logo
	 @mixin ================================== */
.logo {
	display: block;
	&_white {
		@include s('logo'); }
	&_shadow {
		@include s('logo-shadow'); }
	&_color {
		@include r('logo-color'); } }



